.form-group {
	margin: 0 0 15px 0;
}

.form-control,
input[type="text"],
input[type="email"],
input[type="password"],
input[type="search"],
textarea {
	@include form-reset;

	display: block;
	width: 100%;
	font-size: $input-font-size;
	line-height: $input-line-height;
	padding: 8px 15px;
	border: 1px solid rgba($default, 0.4);

	@include transition($transition);

	@include on-event {
		border: 1px solid $primary;
	}

	@include input-placeholder {
		color: rgba($default, 0.6);
	}
}

input[type="submit"],
button {
	@include form-reset;
	@include on-event {
		cursor: pointer;
	}
}

input[type="search"] {
	@include input-search-icons();
}
