@mixin clearfix {
	&:before,
	&:after {
		content: " ";
		display: table;
	}
	&:after {
		clear: both;
	}
}

@mixin on-event($self: false) {
	@if $self {
		&,
		&:hover,
		&:active,
		&:focus {
			@content;
		}
	} @else {
		&:hover,
		&:active,
		&:focus {
			@content;
		}
	}
}

@mixin when-inside($context) {
	#{$context} & {
		@content;
	}
}

@mixin input-placeholder {
	&.placeholder {
		@content;
	}

	&:-moz-placeholder {
		@content;
	}

	&::-moz-placeholder {
		@content;
	}

	&:-ms-input-placeholder {
		@content;
	}

	&::-webkit-input-placeholder {
		@content;
	}
}

@mixin input-search-icons {
	&::-webkit-search-decoration {
		-moz-appearance: none;
		-webkit-appearance: none;
		appearance: none;
	}

	&::-webkit-search-cancel-button {
		-moz-appearance: none;
		-webkit-appearance: none;
		appearance: none;
	}

	&::-webkit-search-results-button {
		-moz-appearance: none;
		-webkit-appearance: none;
		appearance: none;
	}

	&::-webkit-search-results-decoration {
		-moz-appearance: none;
		-webkit-appearance: none;
		appearance: none;
	}
}

@mixin transition($transition) {
	-webkit-transition: $transition;
	-moz-transition: $transition;
	-ms-transition: $transition;
	-o-transition: $transition;
	transition: $transition;
}

@mixin bg-color($color: $theme-colors) {
	@each $color in map-keys($theme-colors) {
		.bg-#{$color} {
			background-color: map-get($theme-colors, $color);
		}
	}
}

@mixin text-color($color: $theme-colors) {
	@each $color in map-keys($theme-colors) {
		.text-#{$color} {
			color: map-get($theme-colors, $color);
		}
	}
}

@mixin media-query($width, $type: min) {
	@if map_has_key($grid-breakpoints, $width) {
		$width: map_get($grid-breakpoints, $width);
		@if $type == max {
			$width: $width - 1px;
		}
		@media only screen and (#{$type}-width: $width) {
			@content;
		}
	}
}

@mixin spacing() {
	@each $prop, $abbrev in (margin: m, padding: p) {
		@each $size, $lengths in $spacers {
			$length-x: map-get($lengths, x);
			$length-y: map-get($lengths, y);

			.#{$abbrev}-#{$size} {
				#{$prop}: $length-y $length-x !important;
			}
			.#{$abbrev}t-#{$size} {
				#{$prop}-top: $length-y !important;
			}
			.#{$abbrev}r-#{$size} {
				#{$prop}-right: $length-x !important;
			}
			.#{$abbrev}b-#{$size} {
				#{$prop}-bottom: $length-y !important;
			}
			.#{$abbrev}l-#{$size} {
				#{$prop}-left: $length-x !important;
			}
		}
	}
}

@mixin make-row($gutter: $grid-gutter-width) {
	--gutter-x: #{$gutter};
	--gutter-y: 0;
	display: flex;
	flex-wrap: wrap;
	margin-top: calc(var(--gutter-y) * -1);
	margin-right: calc(var(--gutter-x) / -2);
	margin-left: calc(var(--gutter-x) / -2);
}

@mixin make-col-ready($gutter: $grid-gutter-width) {
	flex-shrink: 0;
	width: 100%;
	max-width: 100%;
	padding-right: calc(var(--gutter-x) / 2);
	padding-left: calc(var(--gutter-x) / 2);
	margin-top: var(--gutter-y);
}

@mixin make-col($size, $columns: $grid-columns) {
	flex: 0 0 auto;
	width: percentage($size / $columns);
}

@mixin make-col-auto() {
	flex: 0 0 auto;
	width: auto;
}

@mixin make-col-offset($size, $columns: $grid-columns) {
	$num: $size / $columns;
	margin-left: if($num == 0, 0, percentage($num));
}

@mixin row-cols($count) {
	> * {
		flex: 0 0 auto;
		width: 100% / $count;
	}
}

@mixin make-container($breakpoints: $grid-breakpoints) {
	@each $breakpoints in map-keys($breakpoints) {
		@if $breakpoints != xs {
			@include media-query($breakpoints) {
				.container {
					max-width: map-get($grid-breakpoints, $breakpoints);
				}
			}
		}
	}
}

@mixin make-grid-columns($columns: $grid-columns, $gutter: $grid-gutter-width, $breakpoints: $grid-breakpoints) {
	@each $breakpoints in map-keys($breakpoints) {
		@if $breakpoints == xs {
			@if $columns > 0 {
				@for $i from 1 through $columns {
					.col-#{$i} {
						@include make-col($i, $columns);
					}
				}

				@for $i from 0 through ($columns - 1) {
					@if not($breakpoints == "" and $i == 0) {
						.offset-#{$i} {
							@include make-col-offset($i, $columns);
						}
					}
				}
			}
		} @else {
			@include media-query($breakpoints) {
				.col-#{$breakpoints} {
					flex: 1 0 0%;
				}

				.col-#{$breakpoints}-auto {
					@include make-col-auto();
				}

				@if $columns > 0 {
					@for $i from 1 through $columns {
						.col-#{$breakpoints}-#{$i} {
							@include make-col($i, $columns);
						}
					}

					@for $i from 0 through ($columns - 1) {
						@if not($breakpoints == "" and $i == 0) {
							.offset-#{$breakpoints}-#{$i} {
								@include make-col-offset($i, $columns);
							}
						}
					}
				}
			}
		}
	}
}

@mixin make-media-query-display($mqd: $media-query-display, $breakpoint: $grid-breakpoints) {
	@each $breakpoint in map-keys($grid-breakpoints) {
		@if $breakpoint != xs {
			@include media-query($breakpoint) {
				@each $mqd in map-keys($media-query-display) {
					.d-#{$breakpoint}-#{$mqd} {
						display: #{$mqd} !important;
					}
				}
			}
		} @else {
			@each $mqd in map-keys($mqd) {
				.d-#{$mqd} {
					display: #{$mqd} !important;
				}
			}
		}
	}
}

@mixin make-text-alignment {
	@each $breakpoints in map-keys($grid-breakpoints) {
		@if $breakpoints != xs {
			@include media-query($breakpoints) {
				@each $alignment in map-keys($text-alignment) {
					.text-#{$breakpoints}-#{$alignment} {
						text-align: #{$alignment};
					}
				}
			}
		} @else {
			@each $alignment in map-keys($text-alignment) {
				.text-#{$alignment} {
					text-align: #{$alignment};
				}
			}
		}
	}
}

@mixin form-reset {
	border: 0;
	appearance: none;
	outline: none;
}

@mixin align-items-center {
	-webkit-align-items: center;
	-ms-flex-align: center;
	align-items: center;
}

@mixin justify-content-center {
	-webkit-justify-content: center;
	-ms-flex-pack: center;
	justify-content: center;
}

@mixin justify-content-flex-end {
	-webkit-justify-content: flex-end;
	-ms-flex-pack: flex-end;
	justify-content: flex-end;
}

@mixin justify-content-space-between {
	-webkit-justify-content: space-between;
	-ms-flex-pack: justify;
	justify-content: space-between;
}

@mixin align-self-center {
	-ms-flex-item-align: center;
	-webkit-align-self: center;
	align-self: center;
}

@mixin align-self-flex-end {
	-ms-flex-item-align: flex-end;
	-webkit-align-self: flex-end;
	align-self: flex-end;
}
