// Blocks
html,
body,
p,
ol,
ul,
li,
dl,
dt,
dd,
blockquote,
figure,
fieldset,
legend,
textarea,
pre,
iframe,
hr,
h1,
h2,
h3,
h4,
h5,
h6 {
	margin: 0;
	padding: 0;
}

* {
	&,
	&::before,
	&::after {
		box-sizing: border-box;
	}
}

// Box sizing
html {
	box-sizing: border-box;
}

// List
ul {
	list-style: none;
}

// Form
button,
input,
select,
textarea {
	margin: 0;
}

// Media
img,
video {
	height: auto;
	max-width: 100%;
}

// Iframe
iframe {
	border: 0;
}

// Table
table {
	border-collapse: collapse;
	border-spacing: 0;
}

td,
th {
	padding: 0;

	&:not([align]) {
		text-align: inherit;
	}
}
