.PageHeaderSection {
    position: relative;
    padding-top: 84px;
    height: auto;
    padding: 25vh 0 10vh 0;
    border-bottom: 1px solid rgba($black, 0.06);
    @include media-breakpoint-up(lg) {
        padding: 112px 0 0 0;
        height: 100vh;
    }

    .PageHeaderSectionContent {
        height: 100%;
        display: grid;
        grid-template-columns: repeat(1, 1fr);
        grid-template-rows: 1fr;
        grid-column-gap: 0px;
        grid-row-gap: 0px;

        @include media-breakpoint-up(lg) {
            padding-top: 100px;
            grid-template-columns: repeat(6, 1fr);
        }

        &Left {
            padding: 0 20px 0 20px;
            margin: 0 0 35px 0;
            text-align: center;
            @include media-breakpoint-up(lg) {
                text-align: left;
                grid-area: 1 / 1 / 2 / 4;
                padding: 0 0 0 60px;
                margin: 0;
            }

            @media screen and (min-width: 1900px) {
                grid-area: 1 / 2 / 2 / 4;
            }
        }
        &Right {
            padding: 0 20px;
            align-self: center;
            @include media-breakpoint-up(lg) {
                grid-area: 1 / 4 / 2 / 7;
                padding: 0 60px 0 0;
                margin-top: -80px;
            }
            @media screen and (min-width: 1900px) {
                grid-area: 1 / 4 / 2 / 6;
            }
        }
    }

    h1 {
        margin: 0 0 60px 0;
    }

    h6 {
        margin-bottom: 35px;
        @include media-breakpoint-up(lg) {
            margin-bottom: 60px;
        }
        @media screen and (min-width: 1900px) {
            margin-bottom: 100px;
        }
    }

    &Background {
        position: absolute;
        top: 8px;
        right: 0px;
        bottom: 0px;
        width: 100%;
        background-size: cover;
        background-repeat: repeat-y;
        animation: scroll 90s linear infinite;
        z-index: -1;
        @include media-breakpoint-up(lg) {
            top: 112px;
            width: 100%;
        }
    }

    .PageHeaderFrom {
        display: flex;
        flex-direction: row;
        align-items: center;
        box-shadow: 0 0 60px rgba($black, 0.06);
        width: 100%;
        margin: 0 auto;
        @include media-breakpoint-up(sm) {
            width: 80%;
        }
        @include media-breakpoint-up(lg) {
            width: calc(100% - 60px);
        }
        input[type="text"] {
            border: 0;
            padding: 20px 30px;
            border: 0;
            font-family: var(--font-sans-serif);
        }
        a {
            display: flex;
            align-items: center;
            justify-content: center;
            background-color: #23262c;
            color: $white;
            width: 84px;
            height: 64px;
            font-size: 22px;
            transition: background-color 1s cubic-bezier(0.075, 0.82, 0.165, 1);
            @include on-event {
                background-color: #aec4f9;
            }
        }
    }

    .PageHeaderVoiceSearch {
        text-align: center;
        width: 335px;
        height: 335px;
        position: relative;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        margin: 0 auto;
        @include media-breakpoint-up(xl) {
            width: 435px;
            height: 435px;
        }
        span {
            &:first-of-type,
            &:last-of-type {
                position: absolute;
                top: 0;
                left: 0;
                width: 100%;
                height: 100%;
                background-position: center;
                background-repeat: no-repeat;
                background-size: contain;
                z-index: -1;
            }
        }
        h5 {
            font-weight: bold;
        }
        p {
            font-size: 18px;
        }
        i {
            width: 60px;
            height: 60px;
            background-color: #23262c;
            color: $white;
            font-size: 22px;
            display: flex;
            align-items: center;
            justify-content: center;
            border-radius: 50%;
            margin: 0 auto 25px auto;
            transition: background-color 1s cubic-bezier(0.075, 0.82, 0.165, 1);

            &:before {
                position: relative;
                left: 2px;
            }
        }
        a {
            @include on-event {
                i {
                    background-color: #aec4f9;
                }
            }
        }
    }
}
