.WelcomeSection {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: #fafafa;
    z-index: 2;

    &Shape {
        position: absolute;
        left: 50%;
        top: 145px;
        z-index: -1;
        transform: translate3d(0, 0, 0);
        opacity: 0;
        transition: opacity 1s 2s cubic-bezier(0.075, 0.82, 0.165, 1), transform 1s 2s cubic-bezier(0.075, 0.82, 0.165, 1);
    }

    &Logo {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        width: 100%;
        transition: top 1.5s ease-in, left 1.5s ease-in, transform 2s ease-in, width 0.1s ease-in;
        transition-delay: 0.5s;

        span {
            content: "";
            position: absolute;
            top: 0;
            right: -45px;
            width: calc(100% + 45px);
            height: 100%;
            background-color: #fafafa;
            z-index: 1;
        }

        img {
            position: relative;
            width: 80%;
            margin: 0 auto;
            display: block;
            transform: translate3d(45px, 0, 0);
            opacity: 0;
            transition: width 1s cubic-bezier(0.075, 0.82, 0.165, 1);
            @include media-breakpoint-up(md) {
                width: 450px;
            }
        }
    }

    &Container {
        margin: 0 auto;
        max-width: 100%;
        height: 100%;
        padding-left: 20px;
        padding-right: 20px;
        position: relative;

        display: flex;
        align-items: flex-end;
        padding-bottom: 20px;

        @include media-breakpoint-up(sm) {
            max-width: $grid-breakpoint-sm;
        }
        @include media-breakpoint-up(md) {
            max-width: $grid-breakpoint-md;
            padding-bottom: 70px;
        }
        @include media-breakpoint-up(lg) {
            max-width: $grid-breakpoint-lg;
            padding-left: 60px;
            padding-right: 60px;
        }
        @include media-breakpoint-up(xl) {
            max-width: $grid-breakpoint-xl;
        }

        @media screen and (min-width: 1900px) {
            padding-bottom: 120px;
        }

        &Border {
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 0%;
            border-left: 1px solid rgba($black, 0.05);
            border-right: 1px solid rgba($black, 0.05);

            transition: height 1s cubic-bezier(0.075, 0.82, 0.165, 1);
            transition-delay: 1s;
            &:before {
                content: "";
                position: absolute;
                left: 50%;
                transform: translateX(-50%);
                height: 100%;
                width: 1px;
                background-color: rgba($black, 0.05);
            }
        }
    }

    &Content {
        position: relative;
        z-index: 5;

        transition: all 1s 1.5s cubic-bezier(0.075, 0.82, 0.165, 1);

        @include media-breakpoint-up(md) {
            width: 70%;
        }

        h1 {
            font-weight: bold;
            font-size: 40px;
            margin: 0 0 30px 0;

            transform: translateY(15px);
            opacity: 0;

            transition: opacity 1.5s 2.3s cubic-bezier(0.075, 0.82, 0.165, 1), transform 1.5s 2.3s cubic-bezier(0.075, 0.82, 0.165, 1);

            @include media-breakpoint-up(lg) {
                font-size: 60px;
                margin: 0 0 40px 0;
            }

            @media screen and (min-width: 1900px) {
                font-size: 80px;
                margin: 0 0 40px 0;
            }
        }

        .ParagraphOverlay {
            position: relative;

            span {
                content: "";
                position: absolute;
                top: -50px;
                bottom: -50px;
                right: -50px;
                left: 0;
                width: 0%;
                height: calc(100% + 100px);
                background-color: #fafafa;
                z-index: -1;

                transition: width 1s 1.5s cubic-bezier(0.075, 0.82, 0.165, 1);
            }
        }

        p {
            font-size: 14px;
            line-height: 28px;
            position: relative;

            transform: translateY(15px);
            opacity: 0;

            transition: opacity 1.5s 2.3s cubic-bezier(0.075, 0.82, 0.165, 1), transform 1.5s 2.3s cubic-bezier(0.075, 0.82, 0.165, 1);

            &:last-of-type {
                margin: 0 0 40px 0;
            }

            @include media-breakpoint-up(lg) {
                font-size: 17px;
                line-height: 32px;
                &:last-of-type {
                    margin: 0 0 50px 0;
                }
            }

            @media screen and (min-width: 1900px) {
                font-size: 20px;
                line-height: 36px;
                &:last-of-type {
                    margin: 0 0 80px 0;
                }
            }
        }

        a {
            display: flex;
            flex-direction: row;
            align-items: center;
            color: #aec4f9;
            font-size: 18px;
            padding: 0 10px;

            transform: translateY(15px);
            opacity: 0;

            transition: opacity 1.5s 2.3s cubic-bezier(0.075, 0.82, 0.165, 1), transform 1.5s 2.3s cubic-bezier(0.075, 0.82, 0.165, 1), color 0.8s cubic-bezier(0.075, 0.82, 0.165, 1);

            i {
                width: 50px;
                height: 50px;
                display: flex;
                align-items: center;
                justify-content: center;
                background-color: rgba(#aec4f9, 0.2);
                color: #aec4f9;
                border-radius: 50%;
                margin-left: 20px;
                font-size: 20px;
                opacity: 0.5;
                transform: scale(0.9);
                transition: color 0.8s cubic-bezier(0.075, 0.82, 0.165, 1);

                animation-name: animate-btn-arrow;
                animation-duration: 1.7s;
                animation-fill-mode: forwards;
                animation-iteration-count: infinite;
                animation-delay: 0.5s;
            }

            @include on-event {
                color: darken(#aec4f9, 15%);
                i {
                    background-color: rgba(#aec4f9, 0.4);
                    color: darken(#aec4f9, 15%);
                }
            }

            @include media-breakpoint-up(lg) {
                font-size: 18px;
                i {
                    width: 60px;
                    height: 60px;
                    font-size: 20px;
                    margin-left: 25px;
                }
            }

            @media screen and (min-width: 1900px) {
                font-size: 24px;
                i {
                    width: 70px;
                    height: 70px;
                    font-size: 28px;
                    margin-left: 30px;
                }
            }
        }
    }

    &Background {
        position: absolute;
        top: 20px;
        right: 20px;
        bottom: 120px;
        width: 0%;

        background-position: center;
        background-repeat: repeat;
        background-size: cover;

        transition: width 1s 0.5s cubic-bezier(0.075, 0.82, 0.165, 1);

        @include media-breakpoint-up(md) {
            bottom: 20px;
        }

        span {
            content: "";
            position: absolute;
            top: 0px;
            right: 0px;
            bottom: 0px;
            width: 0%;

            animation: scroll 20s linear infinite;
            background: url("/assets/images/bg-gradient.png");
            background-repeat: repeat-y;
            background-size: cover;
            z-index: -1;
            transition: width 1s 1s cubic-bezier(0.075, 0.82, 0.165, 1);
            perspective: 1000px;
            perspective-origin: 50% 50%;
        }
    }
}

@keyframes scroll {
    100% {
        background-position: 0px -2080px;
    }
}

@keyframes animate-btn-arrow {
    0% {
        opacity: 0.5;
        transform: scale(0.9);
    }

    50% {
        opacity: 1;
        transform: scale(1);
    }

    100% {
        opacity: 0.5;
        transform: scale(0.9);
    }
}

.MainContent {
    position: relative;
    z-index: 1;
    &Border {
        display: grid;
        grid-template-columns: repeat(6, 1fr);
        grid-template-rows: 1fr;
        grid-column-gap: 0px;
        grid-row-gap: 0px;

        position: absolute;
        top: 0;
        left: 50%;
        transform: translateX(-50%);
        width: 100%;
        height: 100%;
        z-index: -1;

        transition: height 1s cubic-bezier(0.075, 0.82, 0.165, 1);
        transition-delay: 1s;

        span {
            content: "";
            position: absolute;
            left: 50%;
            transform: translateX(-50%);
            height: 100%;
            width: 1px;
            background-color: rgba($black, 0.05);
            &:nth-of-type(1) {
                visibility: hidden;
                @include media-breakpoint-up(lg) {
                    visibility: visible;
                    grid-area: 1 / 1 / 2 / 3;
                }
            }
            &:nth-of-type(2) {
                grid-area: 1 / 2 / 2 / 6;
            }
            &:nth-of-type(3) {
                visibility: hidden;
                @include media-breakpoint-up(lg) {
                    visibility: visible;
                    grid-area: 1 / 5 / 2 / 7;
                }
            }
        }
    }
}

.MessageIcon {
    position: fixed;
    bottom: 40px;
    right: 40px;

    z-index: 25;

    i {
        color: #23262c;
        font-size: 25px;
        transition: color 1s cubic-bezier(0.075, 0.82, 0.165, 1);
    }

    a {
        width: 60px;
        height: 60px;
        border-radius: 50%;
        display: flex;
        align-items: center;
        justify-content: center;
        background-color: #fff;
        box-shadow: 0 0 40px rgba(0, 0, 0, 0.04);
        transition: background-color 1s cubic-bezier(0.075, 0.82, 0.165, 1);
        @include on-event {
            background-color: #aec4f9;
            i {
                color: $white;
            }
        }
    }
}
