.modal {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 550;

    display: flex;
    align-items: center;
    justify-content: center;

    opacity: 0;
    visibility: hidden;

    transition: opacity 1s cubic-bezier(0.075, 0.82, 0.165, 1);

    &:before {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-color: rgba($black, 0.5);
    }

    &-close {
        position: fixed;
        top: 25px;
        right: 25px;
        z-index: 650;
        a {
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            width: 45px;
            height: 45px;
            background-color: #23262c;
            span {
                width: 25px;
                height: 1px;
                background-color: $white;
                position: relative;
                &:nth-of-type(1) {
                    transform: rotate(45deg);
                    top: 2px;
                }
                &:nth-of-type(2) {
                    transform: rotate(-45deg);
                }
            }
        }
    }

    &-content {
        max-width: 100%;

        @include media-breakpoint-up(sm) {
            max-width: $grid-breakpoint-sm;
        }
        @include media-breakpoint-up(md) {
            max-width: $grid-breakpoint-md;
            padding-bottom: 70px;
        }
        @include media-breakpoint-up(lg) {
            max-width: $grid-breakpoint-lg;
            padding-left: 60px;
            padding-right: 60px;
        }
        @include media-breakpoint-up(xl) {
            max-width: $grid-breakpoint-xl;
        }

        transform: scale(0.8);
        transition: transform 1s cubic-bezier(0.075, 0.82, 0.165, 1);
    }

    &.show {
        opacity: 1;
        visibility: visible;
        .modal-content {
            transform: scale(1);
        }
    }
}
