footer {
    padding: 50px 0;
    background-color: #23262c;
    @include media-breakpoint-up(lg) {
        display: grid;
        grid-template-columns: repeat(6, 1fr);
        grid-template-rows: 1fr;
        grid-column-gap: 0px;
        grid-row-gap: 0px;
        padding: 100px 0;
    }

    h6 {
        font-size: 11px;
        font-weight: bold;
        color: rgba(#fff, 0.7);
        margin: 0 0 15px 0;
        @media screen and (min-width: 1800px) {
            font-size: 14px;
        }
    }

    p {
        font-size: 18px;
        font-weight: 500;
        color: $white;
        @include media-breakpoint-up(xl) {
            width: 70%;
        }
    }

    .links {
        display: flex;
        flex-direction: column;
        li {
            a {
                display: flex;
                flex-direction: row;
                align-items: center;
                padding: 15px 0;
                color: #fff;
                font-weight: bold;
                text-transform: uppercase;
                font-size: 11px;
                transition: color 1s cubic-bezier(0.075, 0.82, 0.165, 1);

                @media screen and (min-width: 1800px) {
                    font-size: 14px;
                }

                i {
                    display: block;
                    transform: rotate(35deg);
                    margin-left: 10px;
                }

                @include on-event {
                    color: #aec4f9;
                }
            }
        }

        &-margin {
            margin-bottom: 30px;
            @include media-breakpoint-up(lg) {
                margin-bottom: 0;
            }
        }
    }

    .socials {
        display: flex;
        flex-direction: row;
        align-items: center;
        margin-top: 80px;
        margin-bottom: 80px;
        @include media-breakpoint-up(lg) {
            margin-bottom: 0;
        }
        li {
            padding: 0 20px;
            h6 {
                margin: 0;
            }
            a {
                color: #fff;
                font-weight: bold;
                font-size: 14px;
                transition: color 1s cubic-bezier(0.075, 0.82, 0.165, 1);

                @include on-event {
                    color: #aec4f9;
                }
            }
            &:first-of-type {
                padding-left: 0;
            }
        }
    }

    .FooterFirstColumn {
        grid-area: 1 / 2 / 2 / 4;
        padding-left: 20px;
        @include media-breakpoint-up(lg) {
            padding-left: 40px;
        }

        @media screen and (min-width: 1800px) {
            padding-left: 60px;
        }
    }

    .FooterSecondColumn {
        grid-area: 1 / 4 / 2 / 6;
    }

    .FooterLinksColumns {
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        align-self: center;
        padding-left: 20px;
        @include media-breakpoint-up(lg) {
            padding-left: 40px;
        }

        @media screen and (min-width: 1800px) {
            padding-left: 60px;
        }

        .FooterLinksColumn {
            width: calc(100% / 2);
            @include media-breakpoint-up("lg") {
                width: calc(100% / 3);
                &:nth-of-type(2) {
                    width: calc(100% / 3.2);
                }

                &:nth-of-type(1) {
                    width: calc(100% / 4);
                }
            }
        }
    }
}

.FooterBottom {
    padding-bottom: 25px;
    background-color: #23262c;
    @include media-breakpoint-up(lg) {
        display: grid;
        grid-template-columns: repeat(6, 1fr);
        grid-template-rows: 1fr;
        grid-column-gap: 0px;
        grid-row-gap: 0px;
    }

    &Left {
        grid-area: 1 / 2 / 2 / 4;
        padding-left: 20px;
        padding-right: 20px;
        text-align: center;
        margin-bottom: 15px;
        @include media-breakpoint-up(lg) {
            padding-left: 40px;
            padding-right: 0;
            text-align: left;
            margin-bottom: 0;
        }

        @media screen and (min-width: 1800px) {
            padding-left: 60px;
        }
    }
    &Right {
        grid-area: 1 / 4 / 2 / 6;
        display: flex;
        flex-wrap: wrap;
        flex-direction: row;
        align-items: center;
        justify-content: center;
        padding: 0 40px;
        @include media-breakpoint-up(md) {
            justify-content: space-between;
        }

        @media screen and (min-width: 1800px) {
            padding-left: 60px;
        }
    }

    p,
    a {
        font-size: 11px;
        font-weight: bold;
        color: #fff;
        margin: 0 0 15px 0;
        text-transform: uppercase;

        @media screen and (min-width: 1800px) {
            font-size: 14px;
        }
    }

    p {
        margin: 0;
    }

    a {
        transition: color 1s cubic-bezier(0.075, 0.82, 0.165, 1);
        margin: 0 5px;
        @include media-breakpoint-up(md) {
            margin: 0;
        }

        @include on-event {
            color: #aec4f9;
        }
    }
}
