@font-face {
    font-family: "icomoon";
    src: url("/assets/fonts/icomoon.eot?gyo159");
    src: url("/assets/fonts/icomoon.eot?gyo159#iefix") format("embedded-opentype"), url("/assets/fonts/icomoon.ttf?gyo159") format("truetype"), url("/assets/fonts/icomoon.woff?gyo159") format("woff"), url("/assets/fonts/icomoon.svg?gyo159#icomoon") format("svg");
    font-weight: normal;
    font-style: normal;
    font-display: block;
}

[class^="icon-"],
[class*=" icon-"] {
    /* use !important to prevent issues with browser extensions that change fonts */
    font-family: "icomoon" !important;
    speak: never;
    font-style: normal;
    font-weight: normal;
    font-variant: normal;
    text-transform: none;
    line-height: 1;

    /* Better Font Rendering =========== */
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

