header {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 150;
    border-bottom: 1px solid rgba($black, 0.05);
    transition: background-color 0.5s cubic-bezier(0.215, 0.61, 0.355, 1);

    .Navbar {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        grid-template-rows: 1fr;
        grid-column-gap: 0px;
        grid-row-gap: 0px;
        width: 100%;

        @include media-breakpoint-up(lg) {
            grid-template-columns: repeat(6, 1fr);
        }

        &Left {
            display: flex;
            align-items: center;
            justify-content: flex-start;
            grid-area: 1 / 1 / 2 / 2;
            padding: 20px;
            img {
                width: 169px;
                display: block;
                margin: 0 auto;
            }
            @include media-breakpoint-up(lg) {
                justify-content: center;
                img {
                    width: 199px;
                }
            }

            @media screen and (min-width: 1990px) {
                padding: 0 60px 0 0;
            }
        }

        &Middle {
            width: 100%;
            display: flex;
            flex-direction: row;
            align-items: flex-start;
            padding: 0 20px;
            justify-content: space-between;

            position: fixed;
            top: 84px;
            left: 0;
            width: 100%;
            height: auto;
            background-color: #fafafa;
            opacity: 0;
            visibility: hidden;
            transform: translateY(25px);

            transition: all 0.5s cubic-bezier(0.215, 0.61, 0.355, 1);

            &.show {
                opacity: 1;
                visibility: visible;
                transform: translateY(0);
            }

            @include media-breakpoint-up(lg) {
                position: relative;
                top: auto;
                left: auto;
                width: auto;
                height: auto;
                background-color: transparent;
                opacity: 1;
                visibility: visible;
                grid-area: 1 / 2 / 2 / 6;
                flex-direction: row;
                align-items: center;
                padding: 0 30px;
                transform: translateY(0);
            }

            @media screen and (min-width: 1900px) {
                padding: 0 60px;
            }

            &Nav,
            &Actions {
                ul {
                    display: flex;
                    flex-direction: column;
                    align-items: flex-start;

                    @include media-breakpoint-up(lg) {
                        flex-direction: row;
                        align-items: center;
                    }
                    li {
                        padding: 0 13px;
                        a {
                            display: block;
                            padding: 15px 0;
                            font-size: 13px;
                            font-weight: bold;
                            text-transform: uppercase;
                            letter-spacing: 1px;
                            color: #23262c;
                            border-top: 2px solid transparent;
                            transition: color 0.5s cubic-bezier(0.215, 0.61, 0.355, 1);
                            &.active {
                                border-top: 2px solid #23262c;
                            }

                            @include on-event {
                                color: #aec4f9;
                            }
                            @include media-breakpoint-up(lg) {
                                padding: 45px 0;
                            }
                        }

                        @include media-breakpoint-up(xl) {
                            padding: 0 20px;
                            font-size: 14px;
                        }

                        @media screen and (min-width: 1600px) {
                            padding: 0 30px;
                        }

                        &.active {
                            a {
                                color: #aec4f9;
                                @include media-breakpoint-up(lg) {
                                    color: #23262c;
                                    border-top: 2px solid #23262c;
                                }
                            }
                        }

                        &:first-of-type {
                            @include media-breakpoint-up(lg) {
                                padding-left: 0;
                            }
                        }

                        &:last-of-type {
                            @include media-breakpoint-up(lg) {
                                padding-right: 0;
                            }
                        }
                    }
                }
            }
        }

        &Right {
            display: flex;
            align-items: center;
            justify-content: flex-end;
            padding-right: 20px;
            grid-area: 1 / 2 / 2 / 3;

            @include media-breakpoint-up(lg) {
                grid-area: 1 / 6 / 2 / 7;
                padding-left: 0;
                padding-right: 0;
                justify-content: center;
            }

            @media screen and (min-width: 1990px) {
                padding: 0 60px 0 0;
            }

            a {
                &:not(.navbar-toggler) {
                    display: none;
                    padding: 16px 40px;
                    font-size: 14px;
                    font-weight: bold;
                    letter-spacing: 1px;
                    text-transform: uppercase;
                    color: $white;
                    background-color: #23262c;
                    transition: background-color 0.5s cubic-bezier(0.215, 0.61, 0.355, 1);
                    @include on-event {
                        background-color: #aec4f9;
                    }
                    @include media-breakpoint-up(lg) {
                        display: inline-block;
                    }
                }
            }
            .navbar-toggler {
                display: flex;
                flex-direction: column;
                align-items: center;
                justify-content: center;
                background-color: #23262c;
                width: 45px;
                height: 45px;
                @include media-breakpoint-up(lg) {
                    display: none;
                }
                span {
                    width: 20px;
                    height: 1px;
                    background-color: $white;
                    margin: 0 0 5px;
                    position: relative;
                    transition: all 0.5s cubic-bezier(0.215, 0.61, 0.355, 1);
                    &:last-of-type {
                        margin: 0;
                    }
                }

                &.active {
                    span {
                        margin: 0;

                        &:nth-of-type(3) {
                            display: none;
                        }
                        &:nth-of-type(1) {
                            top: 1px;
                            transform: rotate(45deg);
                        }
                        &:nth-of-type(2) {
                            transform: rotate(-45deg);
                        }
                    }
                }
            }
        }
    }

    &.nav-scroll {
        background-color: #fafafa;
    }
}
