.ProjectsSection {
    padding-top: 80px;
    padding-bottom: 50px;
    overflow-x: hidden;
    border-bottom: 1px solid rgba($black, 0.05);

    @include media-breakpoint-up(lg) {
        border-right: 20px solid #fafafa;
        border-left: 20px solid #fafafa;
    }

    &Title {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        grid-template-rows: 1fr;
        grid-column-gap: 0px;
        grid-row-gap: 0px;
        margin: 0 0 60px 0;

        @include media-breakpoint-up(lg) {
            grid-template-columns: repeat(6, 1fr);
        }

        &Left {
            padding: 0 20px 0 20px;
            margin: 0 0 35px 0;
            @include media-breakpoint-up(lg) {
                grid-area: 1 / 2 / 2 / 4;
                padding: 0 0 0 60px;
                margin: 0;
            }
        }
        &Right {
            padding: 0 20px;
            display: flex;
            justify-content: flex-end;
            @include media-breakpoint-up(lg) {
                grid-area: 1 / 4 / 2 / 6;
                padding-right: 60px;
            }
        }
    }

    &Slider {
        width: 100%;
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        grid-template-rows: 1fr;
        grid-column-gap: 0px;
        grid-row-gap: 0px;
        @include media-breakpoint-up(lg) {
            padding-left: 60px;
            grid-template-columns: repeat(6, 1fr);
        }

        .ProjectsSlider {
            grid-area: 1 / 1 / 2 / 6;
            @include media-breakpoint-up(xl) {
                grid-area: 1 / 2 / 2 / 6;
            }
        }

        .ProjectsSlide {
            position: relative;
            &Background {
                display: block;
                margin: 0 0 0 auto;
                height: 400px;
                background-position: center;
                background-size: cover;
                background-repeat: no-repeat;
                transition: opacity 0.5s cubic-bezier(0.215, 0.61, 0.355, 1);
                @media screen and (min-width: 1900px) {
                    height: 530px;
                }
            }
            &Content {
                position: absolute;
                top: 60px;
                left: 0;
                padding: 20px;
                background-color: #fafafa;
                opacity: 0;
                transition: opacity 0.5s cubic-bezier(0.215, 0.61, 0.355, 1);
            }
        }

        .swiper-container {
            padding-bottom: 50px;
        }

        .swiper-slide {
            opacity: 0;
            transition: opacity 0.5s cubic-bezier(0.215, 0.61, 0.355, 1);

            span {
                position: absolute;
                top: 0;
                right: 0;
                width: 100%;
                height: 100%;
                background-color: #fafafa;
                z-index: 1;
            }

            .ProjectsSlideContent {
                opacity: 0;
            }
            &-active {
                opacity: 1;
                .ProjectsSlideContent {
                    opacity: 1;
                }
                .ProjectsSlideBackground {
                    @include media-breakpoint-up(xl) {
                        width: calc(100% - 120px);
                    }
                    @media screen and (min-width: 1900px) {
                        width: calc(100% - 200px);
                    }
                }
            }

            &-next {
                opacity: 1;
            }
        }

        .swiper-actions {
            display: flex;
            flex-direction: row;
            align-items: center;
            justify-content: center;
            @include media-breakpoint-up(xl) {
                margin-left: 200px;
                justify-content: flex-start;
            }
            a {
                i {
                    color: #23262c;
                    font-size: 32px;
                }

                &.swiper-button-prev {
                    margin-right: 70px;
                }

                &.swiper-button-next {
                    margin-left: 70px;
                }
            }

            .swiper-pagination {
                display: flex;
                flex-direction: row;
                align-items: center;
            }

            .swiper-pagination-bullet {
                display: block;
                width: 22px;
                height: 2px;
                background-color: rgba($black, 0.1);
                transition: background-color 0.5s cubic-bezier(0.215, 0.61, 0.355, 1);

                @include media-breakpoint-up(sm) {
                    width: 42px;
                }

                @include media-breakpoint-up(md) {
                    width: 82px;
                }

                @media screen and (min-width: 1900px) {
                    width: 122px;
                }

                &-active {
                    background-color: #23262c;
                }
            }
        }
    }
}
