h1,
h2,
h3,
h4,
h5,
h6 {
    color: $text-color;
    line-height: $line-height-base;
    font-weight: $font-weight-base;
}

h1 {
    font-size: 40px;
    line-height: 48px;
    margin: 0 0 30px 0;
    font-weight: 700;

    @include media-breakpoint-up(lg) {
        font-size: 60px;
        line-height: 68px;
        margin: 0 0 40px 0;
    }

    @media screen and (min-width: 1900px) {
        font-size: 77px;
        line-height: 85px;
        margin: 0 0 40px 0;
    }
}

h2 {
    font-size: $h2-font-size;
}

h3 {
    font-size: $h3-font-size;
    font-size: 25px;
    font-weight: 700;
    @include media-breakpoint-up(lg) {
        font-size: 48px;
        line-height: 68px;
    }
}

h4 {
    font-size: $h4-font-size;
}

h5 {
    font-size: 24px;
}

h6 {
    font-weight: 700;
    letter-spacing: 1px;
    text-transform: uppercase;
    font-size: 16px;

    span {
        margin-right: 15px;
    }
}

p {
    font-size: $font-size-base;
    color: $text-color;
    line-height: $line-height-base;
    font-weight: $font-weight-base;
    margin-bottom: $paragraph-margin;

    &.lead {
        font-size: $font-size-base + 0.5rem;
    }
}
